*{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #46529d;
  color: #fff;
}

.text-center{
  text-align: center;
}

.container{
  width: 60%;
  margin: 10% auto;
}

.listitems{
  background-color: white;
  color:  #46529d;
}

.listitems p {
  margin: 0;
  text-align: center;
  padding: 10px 0px;
}

.listitems span{
  display: inline-block;
  text-align: center;
  padding: 10px 0;
  font-size: 10px;
  border: 0.3 solid #ccc;
}

.listitems span.title{
  background-color: turquoise;
  color: #fff;
}

.listitems span.name, 
form input[type=text]{
  width: 60%;
}

.listitems span.age ,
form input[type=number]{
  width: 30%;
}

.listitems span.action ,
form input[type=submit]{
  width: 10%;
}

.listitems span.icon{
  color: #f00;
  cursor: pointer;
}


form input{
  padding:  10px;
  background-color: #ebebeb;
  border: transparent;
  border: 0.3 solid #ccc;
}

form input[type=submit]{
  background-color: #2ebaee;
  border: 0.3 solid #2ebaee;
  color: #fff;
  cursor: pointer;
}